import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import sahil from "../images/sahils6.jpg"

export default function Home({ data }) {
  return (
    <Layout>
      <div>
      <figure style={{ textAlign: "center"}}>
          <img src={sahil} alt="Sahil Shembekar" width="300" height="300"/>
          {/* <figcaption><a href="https://www.google.com">Some Caption</a></figcaption> */}
        </figure>
      </div>
      <p>
        Hey! I am Sahil. I live in Japan and work at <a href="http://www.nachi-fujikoshi.co.jp/eng/rob/index.html" target="_blank">Nachi-Fujikoshi</a>. Before this, I studied robotics at <a href="https://www.cse.sci.waseda.ac.jp/department-en/mmech/" target="_blank">Waseda University</a>. 
        I grew up in Mumbai and previously studied Electronics at <a href="https://tsec.edu/" target="_blank">Mumbai University</a>.
        </p>
       <p>
        Email: <a href="mailto:sahil.shembekar@gmail.com" target="_blank">sahil.shembekar@gmail.com</a>
       </p>
        <p>
          <h3> Elsewhere </h3> 
          <p>
          Connect with me on <a href="https://www.linkedin.com/in/sahil-shembekar/" target="_blank">
            LinkedIn</a>, <a href="https://twitter.com/sahilshems" target="_blank">
            Twitter</a> or <a href="https://www.instagram.com/sahil.shembekar/" target="_blank">Instagram</a>.
          </p>
        </p>
    </Layout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`